@import url(https://fonts.googleapis.com/css2?family=Dokdo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dokdo&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #D9D9D9;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.movie-card {
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 545px;
  margin: auto 20px;
  padding: 10px;
  min-width: 400px;
}

.rate-movie {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

p {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.movie-poster {
  margin: auto;
  width: 270px;
}

.star {
  cursor: pointer;
  padding: 0 3px;
  width: 20px;
}

.movie-details-btn {
  background: white;
  border: 3px solid black;
  cursor: pointer;
  font-size: 1.7em;
  margin-top: 10px;
  padding: 5px 0;
}

.movie-details-btn:hover {
  background: rgb(7, 92, 26);
  color: white;
}

@media only screen and (max-width: 600px) {
  .movie-card {
  margin: 10px auto;
  }
}

.movie-card-page {
  height: 85vh;
}

.movie-card-container {
  background: #D9D9D9;
  display: flex;
  margin: 20px 0;
  overflow: scroll;
}

h2 {
  display: flex;
  margin: 25px;
}

.button-box {
  display: flex;
  justify-content: center;
  margin-top: 1em;
}

.button-box button {
  height: 3em;
  margin-right: 2em;
  padding: 0.5em;
  border: none;
  font-weight: 400;
  font-size: 0.8em;
  border-radius: 5px;
  background-color: #c5ef7c;
}

@media only screen and (max-width: 600px) {
  .movie-card-container {
    flex-direction: column;
  }
}

.details-section, 
.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.details-section {
  height: 90vh;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.title-container,
.movie-details {
  background-color: #FFF;
  width: 30%;
  margin: 1em;
  padding: 0.5em;
}

.movie-details p {
  margin-top: 0.5em;
}

.title-container,
.movie-details,
.details-section button {
  width: 30%;
  min-width: 14em;
  border: 1px solid rgb(7, 92, 26);
  border-radius: 5px;
}

.details-section button {
  background-color: rgb(7, 92, 26);
  color: #FFF;
  font-size: 1.5em;
  padding: 0.2em;
  cursor: pointer;
}

.details-section button:disabled {
  color: gray;
  cursor: not-allowed;
}

.stars-box {
  margin: 0.5em 0;
}

.stars-box img:hover {
  color: yellow;
}

.title-container button {
  background: none;
  border: none;
  color: black;
  font-size: 0.6em;
}


@media only screen and (max-width: 600px) {
  .movie-details {
    height: 100vh;
    width: 80%;
}
  p {
    font-size: 1.4em;
 }
}

.nav-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #0C5A1D;
    padding: 1em;
    height: 15vh;
}

.site-heading {
    color: #c5ef7c;
    font-family: 'Dokdo', cursive;
    font-size: 3em;
    margin: auto 20px;
    text-align: left;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.header-icon-container {
    width: 70%;
    display: flex;
}

.icon {
    height: 5em;
    margin: 0;
}

.nav-login-div {
    width: 15%;
}

.nav-login-link {
    width: 100%;
    height: 2.5em;

}

.user-name {
    color: white;
}

.nav-login-button {
    margin-top: 1em;
    width: 80%;
    height: 2.5em;
    border-radius: 4px;
}

.nav-login-button:hover {
    background-color: yellow;
}
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #C4C4C4
}

.login-image-container {
  width: 50%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.login-img {
  /* width: 600px; */
  height: 70%;
}

h2 {
  font-family: 'Dokdo', cursive;
  font-size: 3em;
}

@media only screen and (max-width: 600px) {
  .login-page {
    flex-direction: column;
    /* justify-content: start; */
    /* height: 100%; */
  }
  .login-image-container {
    display: none;
  }
}

.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}

form {
  width: 90%;
  height: 30em;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: #0C5A1D;
  color: white;

}

input {
  width: 80%;
  height: 5em;
}

.login-link {
  width: 100%;
  height: 3em;
}

.login-container {
  width: 40%;
  height: 3em;
}

.login-button {
  width: 100%;
  height: 3em;
}

.login-button:hover {
  background-color: yellow;
  color: #0C5A1D;
}

@media only screen and (max-width: 600px) {
  .login-container {
    align-items: flex-start;
    height: 100vh;
    padding-top: 20%;
    width: 80%;
  }
  form {
    height: 20em;
  }
  input, placeholder {
    font-size: 1em;
    padding: 3px;
  }
}

